import React from "react";
import "./styles.css";
import axios from "axios";
import { ThemeConsumer } from "react-bootstrap/esm/ThemeProvider";

class Memegenerater extends React.Component {
  constructor() {
    super();
    this.state = {
      toptext: "",
      bottomtext: "",
      randomImg: "http://i.imgflip.com/1bij.jpg",
      file: null
    };
  }
  handlerChange = (event) => {
    let name = event.target.name;
    let value = event.target.value;
    this.setState({ [name]: value });
  };
  handlerChangeFile = (event) => {
    this.setState({
      file: event.target.files[0]
    });
  };

  onFileUpload = (event) => {
    const formData = new FormData();
    formData.append("myimg", this.state.file, this.state.file.name);
    console.log(this.state.file);

    axios.post("../public/images", formData);
  };
  render() {
    return (
      <div>
        <div className="memeForm">
          <form>
            <input
              type="text"
              name="toptext"
              value={this.state.toptext}
              onChange={this.handlerChange}
              className="topText"
            />
            <input
              type="text"
              name="bottomtext"
              value={this.state.bottomtext}
              onChange={this.handlerChange}
              className="bottomText"
            />
            <input type="submit" />
          </form>
        </div>

        <div className="memepic">
          <img src={this.state.file} alt="memeimg" className="img" />
          <h2 className="memetoptext"> {this.state.toptext} </h2>
          <h2 className="memebottomtext"> {this.state.bottomtext} </h2>
        </div>
        <input
          type="file"
          className="upload"
          onChange={this.handlerChangeFile}
          name="myimg"
        />
        <button onClick={this.onFileUpload}> Save </button>
      </div>
    );
  }
}

export default Memegenerater;
